<template>
  <div class="note-page">
    <!-- 展示笔记列表 -->
    <span
      title="笔记列表"
      class="music-list-btn iconfont icon-24px"
      @click="switchNoteList"
    ></span>
    <span
      title="删除全部"
      class="iconfont icon-shanchu deletedAll-btn"
      @click="deletedNoteAll"
    ></span>
    <span
      title="导出"
      class="iconfont icon-Icon-daochu daochu-btn"
      @click="exportLocal"
    ></span>
    <span
      title="导入"
      class="iconfont icon-Icon-daoru daoru-btn"
      @click="importNote"
    ></span>
    <!-- 选择文件框 隐藏不显示，使用js控制点击触发 -->
    <input
      class="downloade_export_ipt"
      title="添加新导入事项，不影响已经存在的事项"
      type="file"
      accept=".txt"
      @change="inputChange($event)"
    />

    <!-- 笔记输入框 -->
    <textarea
      v-model.trim="noteContent"
      class="my-note"
      placeholder="请输入笔记内容"
    ></textarea>
    <!-- 记录按键 -->
    <p class="record-btn" @click="recordData" :class="{ okClick: noteContent }">
      记录
    </p>
    <!-- 笔记列表 -->
    <div class="note-list-box" v-if="isShowList">
      <p class="no-data" v-if="noteContentList.length == 0">暂无笔记</p>
      <div
        class="note-list-item"
        v-for="(item, index) in noteContentList"
        :key="item.note_id"
      >
        <div class="note-title-box">
          <p class="note-date">{{ item.note_date }}</p>
          <p
            class="iconfont icon-shanchu deleted"
            @click.stop="deletedNote(index)"
            title="删除任务"
          ></p>
          <p
            title="添加至任务管理"
            class="iconfont icon-weiwancheng add-taskList"
            @click.stop="addTaskList(item.note_content)"
          ></p>
        </div>

        <h-transitionH>
          <pre class="note-content">{{ item.note_content }}</pre>
        </h-transitionH>
      </div>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  watch
} from 'vue';
import { getLS, setLS, removeLS } from '@/utils/common.js';
export default {
  name: '',
  props: {
    openList: {
      //宽度
      type: Boolean
    }
  },
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      exportLocalTips: '请勿编辑文件NoteList，避免导入错误，',
      isShowList: props.openList,
      noteContent: '', //输入内容
      noteContentList: getLS('fastNote') || [] //获取本地笔记
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    // 导入文件
    const importNote = () => {
      document.getElementsByClassName('downloade_export_ipt')[0].click();
    };
    // 选择文件
    const inputChange = (e) => {
      let resultFile = e.target.files[0]; //获取文件1信息
      // 如果文件存在
      if (resultFile) {
        // 使用 FileReader 来读取文件
        let reader = new FileReader();
        // 读取纯文本文件,且编码格式为 utf-8
        reader.readAsText(resultFile, 'UTF-8');
        // 读取文件,会触发 onload 异步事件,可使用回调函数 来获取最终的值.
        reader.onload = (e) => {
          let fileContent = e.target.result; //文件文本内容
          addNoteFiles(fileContent);
        };
      }
    };
    // 选择文件
    const addNoteFiles = (fileContent) => {
      let idList = []; //保存原有数据所有id
      data.noteContentList.forEach((note) => {
        idList.push(note.note_id);
      });
      //分割换行 转为数组
      const arr = fileContent.split(/[(\r\n)\r\n]+/);
      if (!arr[0].includes(data.exportLocalTips)) {
        console.log('文件格式不正确');
        return;
      }
      arr.shift(); //删除数组第一个日期
      // 字符串转对象
      let noteData = JSON.parse(arr[0]);
      // 保存需要添加的临时数据
      let noteArr = [];
      noteData.forEach((item) => {
        if (idList.length) {
          // 源数据id中是否包含要追加的id
          if (idList.includes(item.note_id)) return;
          noteArr.push(item);
        } else {
          // 没有原始数据就直接追加
          noteArr.push(item);
        }
      });
      // 打开列表
      data.isShowList = true;
      // 追加到笔记列表
      data.noteContentList.unshift(...noteArr);
      // 保存本地
      setLS('fastNote', data.noteContentList);
    };
    // 导出全部任务
    const exportLocal = () => {
      // 导出文件内部title标识
      const title =
        data.exportLocalTips +
        '导出NoteList时间：' +
        new Date().toLocaleString() +
        '\r\n' +
        JSON.stringify(getLS('fastNote'));
      // let allStr = title;
      // // 循环拼接所有任务列表数据
      //   allStr

      //下载内容，标题加本地数据字符串
      const export_blob = new Blob([title]);
      //创建a标签点击可下载
      const save_link = document.createElement('a');
      save_link.href = window.URL.createObjectURL(export_blob);
      //下载时文件名
      save_link.download = 'NoteList.txt';
      fakeClick(save_link);
    };
    // txt文件导出
    const fakeClick = (obj) => {
      const ev = document.createEvent('MouseEvents');
      ev.initMouseEvent(
        'click',
        true,
        false,
        window,
        0,
        0,
        0,
        0,
        0,
        false,
        false,
        false,
        false,
        0,
        null
      );
      obj.dispatchEvent(ev);
    };
    // 记录笔记
    const recordData = () => {
      if (data.noteContent) {
        const noteObj = {
          //创建新的数据对象
          note_id: Date.now().toString(16), //唯一id
          note_date: new Date().toLocaleString(),
          note_content: data.noteContent //内容
        };
        // 追加到数组，保存本地
        data.noteContentList.unshift(noteObj);
        data.noteContent = '';
        setLS('fastNote', data.noteContentList);
      }
    };
    // 切换笔记列表展示
    const switchNoteList = () => {
      data.isShowList = !data.isShowList;
    };
    // 删除笔记
    const deletedNote = (index) => {
      // 再次提示框
      proxy
        .h_confirm({
          title: '提示',
          content: '确认删除笔记 ？',
          style: {
            box: {
              top: 300,
              bgColor: '#fff',
              width: 360,
              padding: 10,
              borderRadius: 4
            },
            content: {
              conColor: 'red'
            }
          }
        })
        .then(() => {
          // 触发删除
          data.noteContentList.splice(index, 1);
          setLS('fastNote', data.noteContentList);
        })
        .catch(() => {
          console.log('取消');
        });
    };
    // 删除全部笔记
    const deletedNoteAll = () => {
      // 再次提示框
      proxy
        .h_confirm({
          title: '提示',
          content: '确认删除全部笔记 ？',
          style: {
            box: {
              top: 300,
              bgColor: '#fff',
              width: 360,
              padding: 10,
              borderRadius: 4
            },
            content: {
              conColor: 'red'
            }
          }
        })
        .then(() => {
          data.noteContentList = [];
          removeLS('fastNote');
        })
        .catch(() => {
          console.log('取消');
        });
    };
    //添加至任务管理
    const addTaskList = (content) => {
      ctx.emit('addTaskList', content);
    };
    return {
      inputChange,
      importNote,
      exportLocal,
      addTaskList,
      deletedNoteAll,
      deletedNote,
      switchNoteList,
      recordData,
      ...toRefs(data)
    };
  }
};
</script>
