<template>
  <div class="switchbox" :class="{ switchR: isswitch }">
    <p class="switchtext switchtext_a" v-if="OptionText[0]">
      {{ OptionText[0] }}
    </p>
    <div class="switchbtn" @click="isswitchFn">
      <p class="switchbtn_info"></p>
    </div>
    <p class="switchtext switchtext_b" v-if="OptionText[1]">
      {{ OptionText[1] }}
    </p>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
export default {
  name: 'SwitchBtn',
  props: {
    Disable: {
      //是否禁用
      type: Boolean,
      default: false
    },
    Switch: {
      //初始值
      type: Boolean,
      default: false
    },
    OptionText: {
      //选项文字
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  setup(props, ctx) {
    const data = reactive({
      isswitch: props.Switch
    });
    const isswitchFn = () => {
      if (!props.Disable) {
        data.isswitch = !data.isswitch;
        ctx.emit('getSwitchVal', data.isswitch);
      }
    };
    return {
      isswitchFn,
      ...toRefs(data)
    };
  }
};
</script>
