<template>
  <div class="tool-box">
    <textarea
      v-model.trim="matterContent"
      class="my-note"
      placeholder="请输入内容"
    ></textarea>
    <div class="tool-btn-box">
      <p
        class="tool-btn-item"
        v-for="item in toolAll"
        :key="item.code"
        @click="handle(item.code)"
      >
        {{ item.text }}
      </p>
    </div>
    <p>{{ result }}</p>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
export default {
  name: '',
  setup() {
    const data = reactive({
      matterContent: '',
      result: '',
      toolAll: [
        { code: 'wordNum', text: '字符统计' },
        { code: 'max', text: '最大数' },
        { code: 'min', text: '最小数' },
        { code: 'summation', text: '求和' },
        { code: 'average', text: '平均数' }
      ]
    });
    onBeforeMount(() => {
      console.log('2.组件挂载页面之前执行----onBeforeMount');
    });
    onMounted(() => {
      console.log('3.-组件挂载到页面之后执行-------onMounted');
    });
    // 执行处理
    const handle = (code) => {
      if (data.matterContent) {
        if (code === 'wordNum') {
          data.result = wordNum();
        } else if (code === 'summation') {
          data.result = summation();
        }
      }
    };
    // 字数统计
    const wordNum = () => {
      return data.matterContent.length;
    };
    // 求和
    const summation = () => {
      const numArr = data.matterContent.split(',');
      let num = 0;
      numArr.forEach((item) => {
        num += parseInt(item);
      });
      return num;
    };
    return {
      handle,
      ...toRefs(data)
    };
  }
};
</script>
