<template>
  <!-- 确认提示框组件 -->
  <div class="confirm">
    <div
      class="confirm_context"
      :style="{
        top: top + 'px',
        opacity: opacity,
        backgroundColor: bgColor,
        width: width + 'px',
        padding: padding + 'px',
        borderRadius: borderRadius + 'px'
      }"
    >
      <!-- 标题 -->
      <p
        class="confirm_tit"
        :style="{
          textAlign: titTextAlign,
          color: titColor,
          lineHeight: titLineHeight + 'px',
          fontSize: titFontSize + 'px',
          fontWeight: titFontWeight
        }"
      >
        {{ title }}
      </p>
      <!-- 内容 -->
      <p
        class="confirm_msg"
        :style="{
          textAlign: conTextAlign,
          color: conColor,
          lineHeight: conLineHeight + 'px',
          fontSize: conFontSize + 'px',
          fontWeight: conFontWeight
        }"
      >
        {{ content }}
      </p>
      <div class="confirm_btn">
        <span
          class="off-btn"
          @click="
            close();
            off();
          "
          v-if="buttonConfigs.no"
          >{{ buttonConfigs.no }}</span
        >
        <span
          class="ok-btn"
          @click="
            ok();
            off();
          "
          v-if="buttonConfigs.yes"
          >{{ buttonConfigs.yes }}</span
        >
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from 'vue';
export default {
  name: 'confirm',
  props: {
    // 标题
    title: {
      type: String,
      default: '标题'
    },
    // 提示信息
    content: {
      type: String,
      default: '提示信息'
    },
    // 弹框样式
    top: {
      type: Number,
      default: 50
    },
    bgColor: {
      type: String,
      default: '#fff'
    },
    width: {
      type: Number,
      default: 300
    },
    padding: {
      type: Number,
      default: 10
    },
    borderRadius: {
      type: Number,
      default: 2
    },
    // 标题样式
    titTextAlign: {
      type: String,
      default: 'center'
    },
    titColor: {
      type: String,
      default: '#000'
    },
    titLineHeight: {
      type: Number,
      default: 40
    },
    titFontSize: {
      type: Number,
      default: 16
    },
    titFontWeight: {
      type: Number,
      default: 700
    },
    // 内容样式
    conTextAlign: {
      type: String,
      default: 'center'
    },
    conColor: {
      type: String,
      default: '#777'
    },
    conLineHeight: {
      type: Number,
      default: 22
    },
    conFontSize: {
      type: Number,
      default: 14
    },
    conFontWeight: {
      type: Number,
      default: 100
    },

    // 按键配置
    buttonConfigs: {
      type: Object,
      default: () => {
        return {
          yes: '确定',
          no: '取消'
        };
      }
    },
    // 确认事件在config.js中定义
    ok: {
      type: Function,
      required: true
    },
    // 取消事件在config.js中定义
    close: {
      type: Function,
      required: true
    }
  },
  setup(props, ctx) {
    // 初始化高度,在传入参数的高度上-50，因为动画移动时会+50
    const top = ref(props.top - 50);
    // 初始化隐藏
    const opacity = ref(0);
    // 显示时
    setTimeout(() => {
      top.value += 50;
      opacity.value = 1;
    }, 0);
    // 确认和取消都隐藏弹窗
    const off = function (params) {
      top.value -= 50;
      opacity.value = 0;
    };

    return {
      top,
      opacity,
      off
    };
  }
};
</script>
<style lang="less" scoped>
/* 外层遮罩层 */
.confirm {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  .confirm_context {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    border-radius: 5px;
    box-sizing: border-box;
    transition: 0.2s;
    .confirm_msg {
      padding: 10px 0;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
    }
    .confirm_btn {
      overflow: hidden;
      margin-top: 20px;
      .ok-btn {
        background: #409eff;
        border-color: #409eff;
        color: #fff;
        &:hover {
          opacity: 0.8;
        }
      }
      .off-btn {
        &:hover {
          color: #409eff;
          border-color: #c6e2ff;
          background-color: #ecf5ff;
        }
      }
    }
    .confirm_btn span {
      margin: 0 5px;
      height: 32px;
      width: 56px;
      line-height: 30px;
      transition: 0.3s;
      float: right;
      cursor: pointer;
      background: #fff;
      border: 1px solid #dcdfe6;
      color: #606266;
      text-align: center;
      box-sizing: border-box;
      font-size: 12px;
      border-radius: 4px;
    }
  }
}
</style>
