<template>
  <div class="music-player-box" :class="{ zoom: !zoomSwitch }">
    <!-- 放大缩小 -->
    <span
      class="zoom-switch iconfont"
      :class="zoomSwitch ? 'icon-xiaochuangkou' : 'icon-quanpingchuangkou'"
      @click="zoomSwitchFn()"
    ></span>
    <!-- 歌单控制按键 -->
    <span
      title="歌单"
      v-show="zoomSwitch"
      class="music-list-btn iconfont icon-24px"
      @click="musicListBtn()"
    ></span>
    <!-- 音乐列表 -->
    <div class="music-list" v-if="isMusicListOpen && zoomSwitch">
      <p
        :class="{ active: currentIndex == index }"
        class="music-list-item"
        v-for="(item, index) in musicData"
        :key="index"
        @click="musicListSwitch(index)"
      >
        {{ item.musicName }}
      </p>
    </div>

    <div class="play-Music">
      <!-- 图片部分 -->
      <div
        class="play-Music-img-box"
        v-show="zoomSwitch"
        @click="playMusic(false)"
      >
        <!-- :class="{start:!isStop}" -->
        <img class="play-Music-img" :src="musicImg" alt="" />
        <div class="switch-play">
          <span
            class="iconfont switch-play-btn"
            :class="!isStop ? 'icon-zanting' : 'icon-danseshixintubiao-'"
          ></span>
        </div>
      </div>
      <!-- 其他信息 -->
      <div class="play-Music-info" :class="{ zoom: !zoomSwitch }">
        <div class="music-title-box">
          <p class="music-name">{{ musicName }}</p>
          <!-- 左右切换歌曲 -->
          <div class="switch-music">
            <span
              class="iconfont icon-danseshixintubiao-1 prev"
              @click="switchMusic('prev')"
            ></span>

            <span
              class="iconfont icon-danseshixintubiao- next"
              @click="switchMusic('next')"
            ></span>
            <span
              class="frequency-switch iconfont icon-yinpin"
              @click="switchFrequency()"
            ></span>
          </div>
        </div>

        <div class="speed-box">
          <!-- 进度条 -->
          <div class="speed-bar">
            <p class="speed-bar-current"></p>
          </div>
          <div class="speed-bar-zzc" @click="changeSpeed"></div>
          <!-- 进度条底部时间 -->
          <div class="speed-time">
            <p class="current-time">{{ timeFormat(currentTime) }}</p>
            <p class="total-time">{{ timeFormat(totalTime) }}</p>
          </div>
        </div>
      </div>
    </div>
    <!-- 歌词显示 -->
    <div class="lyric-box" v-show="zoomSwitch">
      <div class="lyric-tb-zzc"></div>
      <div class="lyric-tb">
        <p class="lyric-item" v-for="(item, index) in lyricData" :key="index">
          {{ item.lyrText }}
        </p>
      </div>
    </div>
    <!-- 音频展示 -->
    <div class="frequency-box" v-show="switchFreBool && zoomSwitch">
      <p
        v-for="(item, index) in frequencyArr"
        :key="index"
        :style="{ height: item - 120 + 'px', left: index * 4 + 'px' }"
        class="frequency-item"
      ></p>
    </div>

    <!-- 播放器主体 -->
    <div id="audio_container"></div>
  </div>
</template>

<script>
import { musicData } from './musicData.js';
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  onBeforeUnmount
} from 'vue';
export default {
  name: '',
  setup(props, ctx) {
    const data = reactive({
      musicData, //音乐数据
      isMusicListOpen: true, //音乐歌单列表
      zoomSwitch: true, //放大缩小
      switchFreBool: false, //动效显示状态
      frequencyArrNum: 100, // 音频数量
      frequencyArr: [], //音频数据
      musicImg: '', //音乐图片地址
      musicName: '', //音乐名称
      imgDeg: 0, //图片旋转角度
      isStop: true, //当前是否停止
      totalTime: '', //音乐总时长
      currentTime: '', //当前播放位置
      times: null,
      degTimes: null,
      currentIndex: 0, //当前音乐下标
      currentSpeedEle: null,
      totalWidth: 0, //进度条总宽度
      lyricData: [], //保存歌词
      lyricTime: [], //歌词时间数组
      lyricTop: 40
    });
    onBeforeMount(() => {});
    onMounted(() => {
      beforePlayMusic();
    });
    // 音乐歌单列表展示按键
    const musicListBtn = () => {
      data.isMusicListOpen = !data.isMusicListOpen;
    };
    // 音乐最小化
    const zoomSwitchFn = () => {
      data.zoomSwitch = !data.zoomSwitch;
    };
    // 切换音乐
    const switchMusic = (type) => {
      // 第一首切换上一个是就切换最后
      if (type === 'prev') {
        if (data.currentIndex === 0) {
          data.currentIndex = musicData.length - 1;
        } else {
          data.currentIndex -= 1;
        }
      }
      //  最后一首下一首就切换至第一首
      if (type === 'next') {
        if (data.currentIndex === musicData.length - 1) {
          data.currentIndex = 0;
        } else {
          data.currentIndex += 1;
        }
      }
      beforePlayMusic();
      playMusic(true); //true代表切换新的一首歌，清空之前播放时间
    };
    // 列表音乐切换
    const musicListSwitch = (index) => {
      data.currentIndex = index;
      beforePlayMusic();
      playMusic(true); //true代表切换新的一首歌，清空之前播放时间
    };
    // 切换动效
    const switchFrequency = () => {
      data.switchFreBool = !data.switchFreBool;
    };
    // 改变进度条
    const changeSpeed = (e) => {
      // 点击位置和总宽度计算获取进度占比
      const percentage = parseInt(e.offsetX) / parseInt(data.totalWidth);
      // 根据点击位置占比计算当前歌曲时间占比
      data.currentTime = data.totalTime * percentage;
      // 根据时间计算时间占比
      const proportion = parseInt(data.currentTime) / parseInt(data.totalTime);
      // 获取进度条运动值
      const speedBarVal = parseInt(data.totalWidth * proportion);
      // 前显示一次当前进度位置 不然之后走定时器会卡顿效果
      data.currentSpeedEle.style.width = speedBarVal + 'px';
      data.isStop = true; //重置开始
      playMusic(false); //改变进度够继续播放
    };
    // 播放前
    const beforePlayMusic = () => {
      // 动态创建audio标签，必变报错
      document.getElementById('audio_container').innerHTML =
        '<audio id="MusicTag"></audio>';
      data.currentTime = 0;
      data.isStop = true; //重置开始
      data.imgDeg = 1; //重置图片旋转角度
      // 预先获取音乐图片
      data.musicImg = musicData[data.currentIndex].img;
      data.musicName = musicData[data.currentIndex].musicName;
      // 进度总宽度
      data.totalWidth =
        document.getElementsByClassName('speed-bar')[0].clientWidth;
      // console.log(data.totalWidth);
      // 当前进度元素
      data.currentSpeedEle =
        document.getElementsByClassName('speed-bar-current')[0];
      // 初始当前进度元素
      data.currentSpeedEle.style.width = '0px';
      lyricHandle(musicData[data.currentIndex].lyric);
    };
    // 歌词处理
    const lyricHandle = (lyricStr) => {
      data.lyricTime = [];
      data.lyricData = [];
      const lyric_a = lyricStr.split('[');
      lyric_a.shift();
      lyric_a.forEach((item) => {
        const lyric_b = item.split(']');
        const lyrTime = lyric_b[0].substring(0, 5);
        const lyrText = lyric_b[1];
        data.lyricTime.push(lyrTime); //歌词时间数组
        data.lyricData.push({ lyrTime, lyrText }); //歌词内容
      });
    };
    // 移动歌词
    const moveLyric = (time, ele) => {
      const index = data.lyricTime.indexOf(timeFormat(time)); //根据时间匹配是第几句词
      let myNum = 40; //初始位置计算每次移动
      if (index !== -1) {
        // 根据下标计算总的移动数量
        data.lyricTop = myNum - index * 20;
        ele.style.top = data.lyricTop + 'px';
      }
    };
    // 点击播放音乐 true从新开始 false继续
    const playMusic = (bool) => {
      //每次暂停开始动态创建audio标签，必变报错
      document.getElementById('audio_container').innerHTML =
        '<audio id="MusicTag"></audio>';
      // 获取音乐播放标签
      const MusicTag = document.getElementById('MusicTag');
      // js控制旋转
      const openBtn = document.getElementsByClassName('play-Music-img')[0];
      //获取歌词外层移动盒子
      const lyricTb = document.getElementsByClassName('lyric-tb')[0];

      // 读取音乐文件
      MusicTag.src = musicData[data.currentIndex].path;
      // ================  获取音频
      let AudioContext = new (window.AudioContext ||
        window.webkitAudioContext)();
      let source = AudioContext.createMediaElementSource(MusicTag);
      let analyser = AudioContext.createAnalyser();
      source.connect(analyser);
      analyser.connect(AudioContext.destination);
      let output = new Uint8Array(data.frequencyArrNum); //这里可以设置获取数组的长度
      // requestAnimationFrame(createAudio);
      // ==================

      if (bool) {
        MusicTag.currentTime = ''; //从头播放音乐
        data.frequencyArr = [];
      } else {
        MusicTag.currentTime = data.currentTime; //设置当前播放时间(需要重置)
      }
      eliminateTime(); //清除定时器
      if (data.isStop) {
        MusicTag.play(); //播放
        // 图片旋转定时器
        data.degTimes = setInterval(() => {
          // 以下是旋转音乐图片js控制，现在使用css
          data.imgDeg += 0.5;
          const d = data.imgDeg + 'deg';
          openBtn.style.transform = 'rotate(' + d + ')';
          // 获取音频展示动效，时间频率要快
          analyser.getByteFrequencyData(output);
          data.frequencyArr = [...output];
          if (!data.switchFreBool) {
            // 截取前50个音频
            ctx.emit('frequencyArrChange', [...output].slice(0, 50));
          } else {
            ctx.emit('frequencyArrChange', []);
          }

          // console.log(data.frequencyArr);
        }, 10);
        // 音乐时间进度条
        data.times = setInterval(() => {
          data.totalTime = MusicTag.duration; //音乐总时长
          data.currentTime = MusicTag.currentTime; //当前播放时长
          moveLyric(MusicTag.currentTime, lyricTb);
          // 计算当前时间占比
          const proportion =
            parseInt(data.currentTime) / parseInt(data.totalTime);
          // 设置进度条运动
          const speedBarVal = parseInt(data.totalWidth * proportion);
          data.currentSpeedEle.style.width = speedBarVal + 'px';
          if (speedBarVal === data.totalWidth) {
            switchMusic('next');
          }
        }, 1000);
      } else {
        MusicTag.pause(); //暂停
      }
      data.isStop = !data.isStop;
    };
    // 时间转化
    const timeFormat = (number) => {
      let minute = parseInt(number / 60);
      let second = parseInt(number % 60);
      minute = minute >= 10 ? minute : '0' + minute;
      second = second >= 10 ? second : '0' + second;
      return minute + ':' + second;
    };
    const eliminateTime = () => {
      clearInterval(data.times); //清除定时器
      clearInterval(data.degTimes); //清除定时器
    };
    onBeforeUnmount(() => {
      eliminateTime();
    });
    return {
      musicListSwitch,
      musicListBtn,
      zoomSwitchFn,
      switchFrequency,
      changeSpeed,
      timeFormat,
      switchMusic,
      playMusic,
      ...toRefs(data)
    };
  }
};
</script>
