<template>
  <!-- 确认提示框组件 -->
  <div class="tips" :class="config.type" id="tips">
    <div class="tips_context" :style="{ top: top + 'px' }">
      <p class="tips_tips" v-if="config.tit">{{ config.tit }}</p>
      <!-- 没传入msg就提示 ?? 新语法 -->
      <p class="tips_msg">{{ config.msg ?? '请输入内容' }}</p>
    </div>
  </div>
</template>
<script>
import { onMounted, ref } from 'vue';
export default {
  name: 'tips',
  props: {
    config: {
      type: Object,
      default: () => {
        return {
          type: 'warning ',
          tit: '标题',
          msg: '输入提示信息内容',
          top: 20,
          time: 2000
        };
      }
    }
  },
  setup(props) {
    onMounted(() => {
      animation();
    });
    const top = ref(-100);
    const animation = function () {
      // 延迟改变top才会有动画效果
      setTimeout(() => {
        //  顶部距离最小20px
        top.value = props.config.top >= 20 ? props.config.top : 20;
      }, 0);
      // // 设置最短显示时间700毫秒
      let newTime = props.config.time < 700 ? 700 : props.config.time;
      setTimeout(() => {
        top.value = -100;
      }, newTime);
    };
    return {
      top,
      animation
    };
  }
};
</script>
<style lang="less" scoped>
/* 外层遮罩层 */
.tips_context {
  width: 300px;
  color: #909399;
  background: #e9e9eb;
  position: fixed;
  left: 50%;
  transform: translate(-50%);
  border-radius: 5px;
  padding: 10px 5px;
  z-index: 999;
  box-sizing: border-box;
  transition: 0.5s;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
  .tips_tips {
    text-align: center;
    line-height: 20px;
    font-weight: 700;
  }
  .tips_msg {
    // margin: 10px 0;
    line-height: 20px;
    font-size: 14px;
    text-align: center;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }
}
.success .tips_context {
  background: #e1f3d8;
  color: #67c23a;
}
.warning .tips_context {
  background: #faecd8;
  color: #e6a23c;
}
.error .tips_context {
  background: #fef0f0;
  color: #f56c6c;
}
.default .tips_context {
  background: #d6f0ff;
  color: #00a2ff;
}
</style>
