
import ScrollContainer from './scrollContainer/scrollContainer.vue'
import Button from './button/button.vue'
import NumAnimation from './numAnimation/numAnimation.vue'
import Pagination from './pagination/pagination.vue'
import Formtable from './formtable/fromtable.vue'
import TransitionH from './transitionHeight/transitionH.vue'
import PortabilityBox from "./portabilityBox/portabilityBox.vue";
import SearchInput from "./searchInput/searchInput.vue";
import Stars from "./stars/stars.vue";
import SwitchBtn from "./switchBtn/switchBtn.vue";
// 事件组件
// 确认提示框的绑定
import Confirm from '../component_module/confirm/confirm.js'
// 提示框的绑定
import Tips from '../component_module/tips/tips.js'



export const UiList = [               //导航数组Confirm
  { EN: "Explain", CH: "说明" },
  { EN: "NumAnimation", CH: "数字动画" },
  { EN: "Button", CH: "按钮" },
  { EN: "Confirm", CH: "确认框" },
  { EN: "Tips", CH: "提示框" },
  { EN: "ScrollContainer", CH: "滚动条" },
  { EN: "Pagination", CH: "分页器" },
  { EN: "Formtable", CH: "表格" },
  { EN: "TransitionH", CH: "高度过度" },

]

// 下面是全部导入  也可以配置第二个参数分别引入
export const SupTopUI = {};
const componentPool = [
  ScrollContainer,
  Button,
  NumAnimation,
  Pagination,
  Formtable,
  TransitionH,
  PortabilityBox,
  SearchInput,
  Stars,
  SwitchBtn
]
// 这里的 options 是可以拿到main.js中注册组件时的第二个参数的配置对象
SupTopUI.install = function (app, options) {
  if (options && options.components) {    //配置options分别引入
    const components = options.components
    components.forEach((componentsName) => {
      componentPool.forEach((component) => {
        if (componentsName == component.name) {
          app.component(component.name, component);
        }
      })
    })
  } else {                            //没配置options全部引入
    componentPool.forEach(component => {
      app.component(component.name, component);
    });
    // 全局事件组件
    app.config.globalProperties.h_confirm = Confirm;
    app.config.globalProperties.h_tips = Tips;
  }

}
