<template>
  <div class="PaginationBox">
    <p class="pagebtn iconfont" @click="PageBtn(true)">&#xe602;</p>
    <div class="Paginationitem" v-for="item in Count" :key="item">
      <!-- 显示省略号 -->
      <div v-if="Count > 10">
        <!-- actval当前选中页  Count总页 66   aroundRange范围4 -->
        <!-- 前页 
                 actval小于 6 时显示
                 每一项  小于 8 的椰树显示     并且     大于64显示   
                 1 2 3 4 5 6 7      ...（65） 和 66

                  item 等于 65 就显示...
                 -->
        <p v-if="actval < aroundRange + 2">
          <span
            :class="{ act: actval == item, noneber: item == Count - 1 }"
            v-if="item < aroundRange + 4 || item > Count - 2"
            @click="getCurrentPage(item)"
            >{{ item == Count - 1 ? ". . . " : item }}</span
          >
        </p>
        <!-- 中间页 
                actval大于 5    并且    actval小于  62             总页数 - aroundRange例如 4 
                每一项  大于actval - 4  并且 小于 actval + 4
                例如当前选中 55      大于actval - 4  并且 小于 actval + 4
                52 53 54   55   56 57 58      显示
                并且 大于64显示   ...66
                    小于3显示    1 ...
                -->
        <p v-if="actval > aroundRange + 1 && actval < Count - aroundRange">
          <span
            :class="{
              act: actval == item,
              noneber: item == Count - 1 || item == 2,
            }"
            v-if="
              (item > actval - aroundRange && item < actval + aroundRange) ||
              item > Count - 2 ||
              item < 3
            "
            @click="getCurrentPage(item)"
            >{{
              item == Count - 1 ? ". . . " : item == 2 ? ". . ." : item
            }}</span
          >
        </p>
        <!-- 后页 
                actval大于  61   总页数 - aroundRange+1 例如 5 
                每一项  小于3显示        大于59 显示  
                1 ...(2)  60 61 62 63 64 65 66
                item 等于 2就显示...
                -->
        <p v-if="actval > Count - (aroundRange + 1)">
          <span
            :class="{ act: actval == item, noneber: item == 2 }"
            v-if="item > Count - (aroundRange + 3) || item < 3"
            @click="getCurrentPage(item)"
            >{{ item == 2 ? ". . . " : item }}</span
          >
        </p>
      </div>
      <!-- 不显示省略 -->
      <p v-if="Count <= 10">
        <span :class="{ act: actval == item }" @click="getCurrentPage(item)">{{
          item
        }}</span>
      </p>
    </div>
    <p class="pagebtn iconfont" @click="PageBtn(false)">&#xe601;</p>
  </div>
</template>
<script>
import { watch, ref } from "vue";
export default {
  name: "h-pagination",
  props: {
    Count: {
      //总页数  要比范围大2倍+2 ##############
      type: Number,
      default: 50,
    },
    aroundRange: {
      //选中页前后范围
      type: Number,
      default: 4,
    },
    currentPage: {
      //初始当前值
      type: Number,
      default: 1,
    },
  },
  setup(props, ctx) {
    const actval = ref(props.currentPage);
    // 监听页数变化
    watch(
      () => props.currentPage,
      () => {
        actval.value = props.currentPage;
      }
    );
    // 页数变化通知父级元素
    watch(actval, () => {
      ctx.emit("getCurrentPage", actval.value);
    });
    // 点击当前页
    const getCurrentPage = function (pageVal) {
      actval.value = pageVal;
    };
    //点击左右按键
    const PageBtn = function (sign) {
      if (sign) {
        //上一页
        if (actval.value !== 1) {
          actval.value--;
        }
      } else {
        //下一页
        if (actval.value !== props.Count) {
          actval.value++;
        }
      }
    };
    return {
      actval,
      PageBtn,
      getCurrentPage,
    };
  },
};
</script>
<style lang="less" scoped>
/* 左右切换按键 */
.pagebtn {
  float: left;
  padding: 0 15px;
  -moz-user-select: none; /*火狐*/
  -webkit-user-select: none; /*webkit浏览器*/
  -ms-user-select: none; /*IE10*/
  -khtml-user-select: none; /*早期浏览器*/
  user-select: none;
}
.pagebtn:hover {
  color: rgb(0, 153, 255);
}
/* 每个页码 */
.Paginationitem span {
  padding: 10px;
  margin: 0 2px;
  -moz-user-select: none; /*火狐*/
  -webkit-user-select: none; /*webkit浏览器*/
  -ms-user-select: none; /*IE10*/
  -khtml-user-select: none; /*早期浏览器*/
  user-select: none;
  border: 1px solid rgb(230, 230, 230);
  border-radius: 5px;
  cursor: pointer;
}
.Paginationitem span.noneber {
  border: none;
}
/* 页码经过 */
.Paginationitem span:hover {
  background: rgb(211, 211, 211);
}
/* 页码选中 */
.Paginationitem span.act {
  background: rgb(35, 112, 255);
  color: #fff;
}
.PaginationBox .Paginationitem {
  float: left;
}
.PaginationBox {
  position: relative;
  line-height: 30px;
  float: left;
  left: 50%;
  transform: translate(-50%);
  background: #fff;
}
</style>
