import {  createVNode, render  } from 'vue'
import tips from './tips.vue';

// 这里是提示框的逻辑
export default config => {
    // 创建临时容器,方便执行后删除
    const temporaryContainer = document.createElement('div')
    // 设置临时容器class
    temporaryContainer.setAttribute('class', 'swh_tips')
    // 挂在body上
    document.body.appendChild(temporaryContainer)
    // 创建虚拟节点，tips时组件 ， config时props接受参数
    const vnode = createVNode(tips,{config})
    // 把虚拟节点渲染container中
    render(vnode ,temporaryContainer)
    // 定时删除 container， 不删除出现会多多余DOM元素
    // 设置最短显示时间
    let newTime = config.time < 700 ? 700 : config.time
    setTimeout(()=>{
        temporaryContainer.remove()
    },newTime + 500)
}

  
