<template>
  <!-- 贪吃蛇游戏组件 -->
  <div class="gluttonous-page">
    <!-- 右下角方向盒子 -->
    <div class="direction-box">
      <!-- 4个方向按键 -->
      <span
        v-if="gamePlayerNum === '1'"
        @click="direction(item)"
        v-for="item in directionArr[0]"
        :key="item"
      ></span>
    </div>
    <!-- 游戏顶部积分 -->
    <div class="gluttonous-title">
      <!-- 积分展示 -->
      <span
        class="game-fraction"
        :class="'fraction' + index"
        v-for="(num, index) in gameFraction"
        :key="index"
        >{{ num }}</span
      >
      <div class="config-box">
        <span class="config-text">食物</span>
        <div class="config-btn">
          <span
            class="iconfont icon-qiehuanqizuo prev"
            @click="switchFoodNum('prev')"
          ></span>
          <span class="food-num">{{ foodNum }}</span>
          <span
            class="iconfont icon-qiehuanqiyou next"
            @click="switchFoodNum('next')"
          ></span>
        </div>
      </div>
      <div class="config-box">
        <span class="config-text">自撞</span>
        <div class="config-btn">
          <SwitchBtn
            @getSwitchVal="getSwitchBumpOwn"
            :Disable="!isStop"
            :Switch="isBumpOwn"
          />
        </div>
      </div>
      <div class="config-box">
        <span class="config-text">边界</span>
        <div class="config-btn">
          <SwitchBtn
            @getSwitchVal="getSwitchBoundary"
            :Disable="!isStop"
            :Switch="isBoundary"
          />
        </div>
      </div>
    </div>
    <!-- 游戏日志 -->
    <div class="game-journal">
      <p
        :class="['journal' + index]"
        v-for="(item, index) in bumpText"
        :key="index"
      >
        {{ item }}
      </p>
    </div>
    <!-- 封面logo、开始按键、规则 -->
    <div class="cover-box" v-if="isStop">
      <!-- logo图 -->
      <img src="~@/assets/img/gluttonous1.png" alt="" />
      <!-- 开始按键 -->
      <p class="start-btn" @click="startGame">
        {{ gameOver ? '重新开始' : '开始游戏' }}
      </p>
      <div class="game-rule-box">
        <p class="rule-text">
          <span>游戏玩法介绍：</span>
        </p>
        <p class="rule-text">
          <span class="key-btn long">回车</span>
          <span class="key-btn long">空格</span>
          切换开始或暂停游戏
        </p>
        <p class="rule-text">
          <span>切换玩家数量：</span>
          <span class="key-btn">1</span>
          <span class="key-btn">2</span>
          <span class="key-btn">3</span>
          <span class="key-btn">4</span>
        </p>
        <p class="rule-text">
          <span>游戏暂停或结束时可切换玩家数量</span>
        </p>
        <p
          class="rule-text"
          v-for="(dir, dirIndex) in directionRule"
          :key="dirIndex"
        >
          <span>{{ overTipsText[gamePlayer[dirIndex]] }}蛇玩家方向：</span>
          <span class="key-btn" v-for="(item, index) in dir" :key="index">{{
            item
          }}</span>
        </p>
        <p class="rule-text">
          <span>玩家OVER条件：</span>
        </p>
        <p class="rule-text">
          <span>1.贪吃蛇头部撞到边界（可调）</span>
        </p>
        <p class="rule-text">
          <span>2.贪吃蛇头部撞到自己身体（可调）</span>
        </p>
        <p class="rule-text">
          <span>3.贪吃蛇头部撞到其他蛇身体</span>
        </p>
      </div>

      <!-- <p class="game-over" v-if="gameOver">游戏结束</p> -->
      <p class="over-text" :class="overText" v-if="overText">
        {{ overTipsText[overText] + '方OVER' }}
      </p>
    </div>
    <!-- 贪吃蛇场地 && 食物 -->
    <div class="gluttonous-range">
      <!-- 食物 -->
      <p
        v-for="(food, index) in foodEleArr"
        :key="index"
        class="food-ele"
        :style="{ top: food[0] + 'px', left: food[1] + 'px' }"
      ></p>

      <!-- 贪吃蛇 -->
      <div
        class="gluttonous-body"
        :class="gluttonousBody[index]"
        v-for="(item, index) in gluttonousEle"
        :key="index"
      >
        <p
          class="gluttonous-body-item"
          :class="[currentDir[index], gluttonousBodyItem[index]]"
          :style="{ top: body.top + 'px', left: body.left + 'px' }"
          v-for="(body, bodyIndex) in item"
          :key="bodyIndex"
        ></p>
      </div>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  onBeforeUnmount
} from 'vue';
import { randomNum } from '@/utils/common.js';
export default {
  name: '',
  setup() {
    const data = reactive({
      //相撞信息
      bumpText: [],
      // 方向规则
      directionRule: [
        ['↑', '↓', '←', '→'],
        ['w', 's', 'a', 'd'],
        ['i', 'k', 'j', 'l'],
        ['8', '5', '4', '6']
      ],
      // 失败提示文字级样色
      overTipsText: {
        a: '红',
        b: '蓝',
        c: '紫',
        d: '黑'
      },
      gamePlayer: ['a', 'b', 'c', 'd'],
      //4个不同身体的类名字典
      gluttonousBody: [
        '',
        'gluttonous-body_B',
        'gluttonous-body_C',
        'gluttonous-body_D'
      ],
      //4个不同身体的类名字典
      gluttonousBodyItem: [
        '',
        'gluttonous-body-item_B',
        'gluttonous-body-item_C',
        'gluttonous-body-item_D'
      ],
      // 4个玩家对象按键1234
      directionArr: [
        ['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'],
        ['w', 's', 'a', 'd'],
        ['i', 'k', 'j', 'l'],
        ['8', '5', '4', '6']
      ],

      overText: '', //失败提示
      gamePlayerNum: '2', //玩家数量
      gameOver: false, //游戏是否结束
      isStop: true, //是否停止
      walkTimes: null,
      foodEleArr: [], //食物定位  距离边界
      existencePerson: 0, //生存着数量
      // 可配置
      foodNum: 10, //食物数量
      isBoundary: true, //是否有边界
      isBumpOwn: true, //是否有撞自己
      initialLong: 10, //初始身体长度

      // 一下是多个玩家数据
      gameFraction: [], //游戏分数
      speedGrade: [], //初始速度等级 1-10
      currentDir: [], //当前方向，控制头部样式
      gluttonousEle: [], //贪吃蛇身体元素数组
      dirType: [] //行走方向   默认 →
    });

    // 速度等级列表
    const speedConfig = [];
    onBeforeMount(() => {});
    onMounted(() => {
      watchKeydown(); //监听按键
      initial(); //初始化
    });
    // 监听按键
    const watchKeydown = () => {
      // 监听按键
      window.addEventListener('keydown', (e) => {
        e = e || window.event;
        const btn = e.key;
        // 监听设置玩家个数
        if (['1', '2', '3', '4'].includes(btn) && data.isStop) {
          data.gamePlayerNum = btn;
          initial(); //初始化
        }
        // 回车空格停止开始游戏
        if (btn === 'Enter' || btn === ' ') {
          1;
          // console.log(data.isStop);
          if (data.isStop) {
            startGame();
          } else {
            //回车停止游戏
            stopGames();
          }
        } else {
          // 游戏停止时，不改变方向
          if (data.isStop) return;
          // 监听所有玩家方向按键
          data.directionArr.forEach((d) => {
            if (d.includes(btn)) {
              // 获取对应下标控制方向
              direction(btn);
            }
          });
        }
      });
    };
    // 切换食物数量
    const switchFoodNum = (type) => {
      if (data.isStop) {
        if (type === 'next') {
          if (data.foodNum === 20) return;
          data.foodNum += 1;
        } else {
          if (data.foodNum === 7) return;
          data.foodNum -= 1;
        }
        data.foodEleArr = [];
        initialAllFood();
      }
    };
    // 是否开启边界
    const getSwitchBoundary = (val) => {
      data.isBoundary = val;
    };
    // 是否开启自撞
    const getSwitchBumpOwn = (val) => {
      data.isBumpOwn = val;
    };
    // 初始化贪吃蛇长度
    const initial = () => {
      data.existencePerson = parseInt(data.gamePlayerNum);
      data.overText = ''; //失败提示
      data.gameOver = false; //游戏是否结束
      data.walkTimes = null;
      data.bumpText = [];
      data.foodEleArr = [];
      data.gluttonousEle = [];
      data.speedGrade = [];
      data.gameFraction = [];
      data.dirType = [];
      data.currentDir = [];
      // 生成贪吃蛇身体,判断几个玩家，生成几个对应身体
      for (let n = 0; n < data.gamePlayerNum; n++) {
        let gluttonousList = []; //每条蛇身体
        for (let i = 0; i < data.initialLong; i++) {
          gluttonousList.push({ top: 380 + n * 50, left: 700 - i }); //1号玩家
        }
        data.gluttonousEle.push(gluttonousList); //保存每条蛇
        data.speedGrade.push(1); //保存每条初始速度
        data.gameFraction.push(0); //每个玩家积分数组
        data.dirType.push(['left', true]); //每个玩家方向
        data.currentDir.push(data.directionArr[0][3]); //设置  统一ArrowRight字段，向右
      }
      // 保存初始速度，公用的
      let initSpeed = data.speedGrade[0];
      // 生成速度1-10 每个等级加0.1速度
      for (let i = 0; i < 90; i++) {
        const inSpeed = parseFloat((initSpeed += 0.1).toFixed(1));
        for (let a = 0; a < 10; a++) {
          speedConfig.push(inSpeed);
        }
      }
      initialAllFood(); //初始化食物
    };
    // 初始化全部食物
    const initialAllFood = () => {
      // 初始化对应数量食物
      for (let food = 0; food < data.foodNum; food++) {
        data.foodEleArr.push(foodTL());
      }
    };
    // 开始游戏
    const startGame = () => {
      // 游戏结束后从新开始游戏
      if (data.gameOver) {
        initial(); //初始化游戏
      }
      data.isStop = false;
      // setInterval
      // setTimeout
      data.walkTimes = setInterval(() => {
        walk();
      }, 10);
    };
    // 方向控制
    const direction = (type) => {
      let currentDir; //临时方向
      let dirType; //临时变量保存行走方向
      let gamePlayerIndex; //保存当前玩家下标
      data.directionArr.forEach((item, index) => {
        const dirIndex = item.indexOf(type); //判断每个玩家对应的方向数组的位置，转成玩家1的方向标识
        if (dirIndex !== -1) {
          gamePlayerIndex = index; //保存当前第几个玩家
          currentDir = data.directionArr[0][dirIndex]; //无论哪个玩家，方向键都统一为 ArrowUp组的字段
        }
      });
      // 判断方向设置行走方向
      switch (currentDir) {
        case 'ArrowUp': //上
          dirType = ['top', false];
          break;
        case 'ArrowDown': //下
          dirType = ['top', true];
          break;
        case 'ArrowLeft': //左
          dirType = ['left', false];
          break;
        case 'ArrowRight': //右
          dirType = ['left', true];
          break;
      }
      // 不能直接掉头
      if (data.dirType[gamePlayerIndex][0] === dirType[0]) return;
      data.currentDir[gamePlayerIndex] = currentDir; //保存方向
      data.dirType[gamePlayerIndex] = dirType; //行走方向
    };
    // 贪吃蛇每一步
    const walk = () => {
      // 保存贪吃蛇原始数据
      // 1号玩家
      data.gluttonousEle.forEach((strip, stripIndex) => {
        const copyEle = JSON.parse(JSON.stringify(strip));
        if (!strip) return;
        strip.forEach((body, index) => {
          if (index) {
            // 设置除头部外其他继承上一个位置
            strip[index] = copyEle[index - 1];
          } else {
            // index===0 代表头部 第一个设置增加步数1
            // 获取方向
            let headerEle = data.dirType[stripIndex][0];
            //  判断方向true或false
            if (data.dirType[stripIndex][1]) {
              body[headerEle] += data.speedGrade[stripIndex]; //↓  →
            } else {
              body[headerEle] -= data.speedGrade[stripIndex]; //↑  ←
            }
            // 设置头部位置
            strip[index] = body;
          }
        });
      });

      eatFood(); // 吃到食物逻辑
    };
    // 吃到食物逻辑，或者撞到自己
    const eatFood = () => {
      data.gluttonousEle.forEach((strip, stripIndex) => {
        if (!strip) return;
        const inTail = strip[strip.length - 1]; //尾部
        const inheader = strip[0]; //头部
        // 循环食物判断是否吃到
        data.foodEleArr.forEach((food, index) => {
          if (
            inheader.top > food[0] - 10 &&
            inheader.top < food[0] + 10 &&
            inheader.left > food[1] - 10 &&
            inheader.left < food[1] + 10
          ) {
            data.gameFraction[stripIndex]++;
            // 头部和食物在一定范围内就代表吃到，可能会触发多次（但是触发一次就会清除食物位置，所以不会出问题）
            // 直接增加5长度，可控制，追加当前末尾元素
            // 增加身体长度
            let addBody = [];
            for (let i = 0; i < 5; i++) {
              addBody.push(inTail);
            }
            strip.push(...addBody);
            //根据长度，设置匹配速度等级表的速度 -11是包含初始10的长度 ,超过速度重置为1速度
            data.speedGrade[stripIndex] = speedConfig[strip.length - 11] || 1;
            generateFood(index); //补充食物
          }
        });
      });
      // 判断游戏结束
      isGameOver();
    };
    // 判断游戏是否结束（撞到自己身体，或者墙壁）
    const isGameOver = () => {
      // 判断是否撞到自己或撞墙
      data.gluttonousEle.forEach((strip, stripIndex) => {
        if (!strip) return;
        const inheader = strip[0]; //头部
        if (data.isBumpOwn) {
          strip.forEach((item, index) => {
            // 头部元素和除本身外元素位置相等就撞到自己
            // index == 0 是自己
            if (index) {
              if (inheader.top == item.top && inheader.left == item.left) {
                gameOverFn(data.gamePlayer[stripIndex], '1');
              }
            }
          });
        }

        // 禁止判断是否撞墙
        if (!data.isBoundary) return;
        // 判断是否撞墙
        if (
          inheader.top > 760 ||
          inheader.top < 0 ||
          inheader.left > 1850 ||
          inheader.left < 0
        ) {
          gameOverFn(data.gamePlayer[stripIndex], '2');
        }
      });

      // 2名玩家，就判断是否互相撞到
      if (data.existencePerson > 1) {
        // 要撞的玩家
        data.gluttonousEle.forEach((strip_bump, bumpIndex) => {
          if (!strip_bump) return;
          const bumpHeader = strip_bump[0];
          // 被撞的其他玩家
          try {
            data.gluttonousEle.forEach((strip_other, otherIndex) => {
              if (!strip_other) return;
              // 除去和自己判断是否相撞，上面已经判断过
              if (bumpIndex === otherIndex) return;
              // 这里只和其他玩家每个身体相撞
              strip_other.forEach((body) => {
                if (
                  bumpHeader.top > body.top - 10 &&
                  bumpHeader.top < body.top + 10 &&
                  bumpHeader.left > body.left - 10 &&
                  bumpHeader.left < body.left + 10
                ) {
                  // data.gamePlayer[otherIndex]被撞玩家下标，可以加分
                  gameOverFn(
                    data.gamePlayer[bumpIndex],
                    data.gamePlayer[otherIndex]
                  ); //失败方
                  throw new Error('相撞终止，避免多余循环');
                }
              });
            });
          } catch (e) {
            console.log(e);
            return;
          }
        });
      }
    };
    // 游戏结束执行
    const gameOverFn = (type, typeOth) => {
      // 每次over信息提示日志
      let overText = '';
      if (typeOth === '1') {
        // 撞到自己
        overText = `${data.overTipsText[type]}蛇撞到自己OVER`;
      } else if (typeOth === '2') {
        // 撞到边界
        overText = `${data.overTipsText[type]}蛇撞到边界OVER`;
      } else {
        //玩家相撞
        overText = `${data.overTipsText[type]}蛇撞到${data.overTipsText[typeOth]}蛇：${data.overTipsText[type]}蛇OVER`;
      }
      data.bumpText.unshift(overText);
      // 有多个玩家时，一个over不暂停游戏提示
      if (data.existencePerson - 1) {
        const overIndex = data.gamePlayer.indexOf(type);
        // over玩家后，响应数组位置替换为null
        data.gluttonousEle.splice(overIndex, 1, null);
        // 减去生存玩家数量
        data.existencePerson -= 1;
      } else {
        // 最后一个玩家over暂停游戏
        data.overText = type; //判断失败方
        data.gameOver = true; //游戏结束
        stopGames(); //停止清除定时器
      }
    };
    // 停止游戏
    const stopGames = () => {
      data.isStop = true;
      clearInterval(data.walkTimes); //清除定时器
    };
    // 补充随机食物
    const generateFood = (index) => {
      // 补充食物
      data.foodEleArr.splice(index, 1, foodTL());
    };
    // 随机食物位置
    const foodTL = () => {
      return [randomNum(20, 730), randomNum(20, 1820)];
    };
    // 组件取消挂载
    onBeforeUnmount(() => {
      stopGames(); //停止清除定时器
    });
    return {
      switchFoodNum,
      getSwitchBumpOwn,
      getSwitchBoundary,
      direction,
      startGame,
      stopGames,
      ...toRefs(data)
    };
  }
};
</script>
