<template>
  <!-- 可移动元素弹窗-->
  <div
    class="portability-zzc"
    v-if="openPortability"
    :class="{ cancel: isZzc }"
    :style="{ zIndex: zIndex }"
  >
    <div
      class="portability"
      :class="{ cancel: !isBgColor }"
      :style="'top:' + top"
      ref="targetEle"
    >
      <div
        class="portability-top-box"
        :class="{ cancel: !isBgColor }"
        @mousedown.self="dragDrop"
      >
        {{ title }}
        <p
          class="iconfont icon-31guanbi portability-off"
          @click="closePortability"
        ></p>
      </div>
      <div class="portability-center-box" :class="{ noPadding }">
        <slot>自定义模板内容插槽</slot>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { moveEle } from '@/utils/common.js';
import { default_type } from 'mime';
export default {
  name: 'h-portabilityBox',
  props: {
    openPortability: {
      type: Boolean
    },
    title: {
      type: String,
      default: '可移动弹窗'
    },
    // 默认顶部距离
    top: {
      type: String,
      default: '50px'
    },
    zIndex: {
      //多个弹窗控制层级
      type: Number,
      default: 2
    },
    isZzc: {
      //是否显示遮罩层
      type: Boolean,
      default: true
    },
    // 容器是否有内边距
    noPadding: {
      type: Boolean,
      default: false
    },
    // 是否显示背景颜色
    isBgColor: {
      type: Boolean,
      default: true
    }
  },

  setup(props, ctx) {
    const data = reactive({
      targetEle: null //保存要移动的元素
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    // 按下弹窗移动区域
    const dragDrop = (operationEle) => {
      // 要移动元素和可移动区域
      moveEle(data.targetEle, operationEle); //移动元素方法
    };
    const closePortability = () => {
      ctx.emit('closePortability');
    };
    return {
      closePortability,
      dragDrop,
      ...toRefs(data)
    };
  }
};
</script>
<style lang="less" scoped>
.portability-zzc {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  // background: rgb(108, 108, 108);
  &.cancel {
    height: auto;
    width: auto;
  }
  .portability {
    position: fixed;
    background: #fff;
    border-radius: 3px;
    left: 50%;
    transform: translate(-50%);
    z-index: 777;
    box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.3);
    &.cancel {
      background: none;
    }
    .portability-top-box {
      height: 40px;
      width: 100%;
      background: #fff;
      border-bottom: 1px solid rgb(248, 248, 248);
      cursor: move;
      line-height: 40px;
      padding-left: 20px;
      box-sizing: border-box;
      font-size: 14px;
      &.cancel {
        background: none;
        border-bottom: none;
        color: #fff;
      }
      .portability-off {
        float: right;
        line-height: 40px;
        margin-right: 10px;
        font-size: 16px;
        cursor: pointer;
        &:hover {
          color: @TC;
        }
      }
    }
    .portability-center-box {
      padding: 10px 20px;
    }
    .noPadding {
      padding: 0;
    }
  }
}
</style>
