<template>
  <table class="Formtable" width="100%">
    <tr
      :style="{
        lineHeight: titLineHeight + 'px',
        fontSize: titFontSize + 'px',
      }"
    >
      <th
        v-for="(tit, index) in titData"
        :key="tit"
        :style="{ width: SetWidth(index) }"
      >
        {{ tit }}
      </th>
    </tr>
    <tr
      :style="{
        lineHeight: conLineHeight + 'px',
        fontSize: conFontSize + 'px',
        color: conColor,
      }"
      v-for="(con, index) in contentData"
      :key="index"
    >
      <td v-for="item in con" :key="item">
        {{ item }}
      </td>
    </tr>
  </table>
</template>

<script>
export default {
  name: "h-formtable",
  props: {
    // 内容行高
    conLineHeight: {
      type: Number,
      default: 20,
    },
    // 内容字号
    conFontSize: {
      type: Number,
      default: 14,
    },
    // 内容颜色
    conColor: {
      type: String,
      default: "#777",
    },
    // 头部行高
    titLineHeight: {
      type: Number,
      default: 20,
    },
    // 头部字号
    titFontSize: {
      type: Number,
      default: 14,
    },
    // 指定列宽度
    colWidth: {
      type: Object,
      default: () => {
        return {
          1: "20%",
          2: "40%",
          4: "20%",
        };
      },
    },
    // 表头信息
    titData: {
      type: Array,
      default: () => {
        return ["参数", "说明", "类型", "可选值", "默认值"];
      },
    },
    // 表格内容
    contentData: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup(props, ctx) {
    // 设置指定列宽度宽度
    const SetWidth = function (index) {
      // index从0 开始+1对应表格列数
      return props.colWidth[index + 1];
    };
    //判断表格内容是否适合,提示错误信息
    const paramsError = function () {
      const cData = props.contentData;
      const tData = props.titData;
      if (cData.length == 0) {
        console.warn("Formtable 未接收到表格内容");
        ctx.emit("errorEvent", "nullData");
      } else {
        for (let i = 0; i < cData.length; i++) {
          if (cData[i].length !== tData.length) {
            // 表格错误信息
            console.warn(
              "Formtable 单行内容个数不等于表头内容个数，表格无法对齐"
            );
            ctx.emit("errorEvent", "errorData");
          }
        }
      }
    };
    paramsError(); //判断表格内容是否适合
    return {
      SetWidth,
    };
  },
};
</script>

<style lang="less" scoped>
.Formtable {
  border-radius: 5px;
  border-collapse: collapse; /*关键代码*/
  th,
  td {
    text-align: left;
    padding: 20px 10px;
    box-sizing: border-box;
    border-bottom: 1px solid rgb(231, 231, 231);
  }
  th {
    background: rgb(248, 248, 248);
  }
}
</style>
