<template>
  <div class="gobang">
    <div class="gobang-title">
      <p class="black-chess-num" :class="{ current: currentPlayChess == 1 }">
        黑棋子 : {{ blackChess }}
      </p>
      <p class="start-btn" @click="initCoordinate">从新开始</p>
      <p class="fallback-chess" @click="fallbackChess">
        悔棋次数 : 黑 {{ blackLatelyNum }} 白 {{ whiteLatelyNum }}
      </p>
      <p class="white-chess-num" :class="{ current: currentPlayChess == 2 }">
        白棋子 : {{ whiteChess }}
      </p>
    </div>
    <div class="formtable-box">
      <p
        class="victory-tips"
        :class="
          victoryTips
            ? victoryTips === '黑方胜利'
              ? 'show black'
              : 'show white'
            : ''
        "
      >
        <span v-for="item in victoryTips" :key="item">{{ item }}</span>
      </p>
      <!-- 棋盘 -->
      <table class="formtable">
        <tr v-for="itema in checkerboardNum" :key="itema">
          <td v-for="itemb in checkerboardNum" :key="itemb"></td>
        </tr>
      </table>
      <!-- 棋盘点位 -->
      <div class="chess-position-box">
        <p v-for="(item, index) in coordinate" :key="index">
          <span
            class="chess-position"
            :class="judgeChessType(item.pieceType)"
            @click="playChess(index, item.edgeRange)"
          ></span>
        </p>
      </div>
    </div>
    <div class="gobang-bottom">
      <p class="victory-num">{{ blackVictoryNum }}胜</p>
      <p class="time-num">游戏时间：{{ totalTimeNum }}</p>
      <p class="time-num">本局时间：{{ bureauTimeNum }}</p>
      <p class="victory-num">{{ whiteVictoryNum }}胜</p>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, onBeforeUnmount } from 'vue';
import { formatDuring } from '@/utils/common.js';
export default {
  name: '',
  setup() {
    const data = reactive({
      victoryTips: '', //保存胜利的
      blackVictoryNum: 0, //黑方胜利次数
      whiteVictoryNum: 0, //白方胜利次数
      pointNum: 225, //总点位数量
      blackChess: 113, //黑棋数量
      whiteChess: 112, //白棋数量
      checkerboardNum: 14, //棋盘格子数量14*14（点位15*15）
      currentPlayChess: 1, //当前要下棋子类型     1黑    2白
      blackLatelyNum: 0, //黑色悔棋次数
      whiteLatelyNum: 0, //白色悔棋次数
      coordinate: [], //保存棋盘上所有点位对象信息
      totalTimeNum: formatDuring(0), //总计时
      bureauTimeNum: formatDuring(0), //本局计时
      totalTimes: null, //总游戏时间
      bureauTimes: null, //本局游戏时间
      //边界点位定义（优化   自动生成）
      boundary: {
        0: 'Ledge',
        14: 'Redge',
        15: 'Ledge',
        29: 'Redge',
        30: 'Ledge',
        44: 'Redge',
        45: 'Ledge',
        59: 'Redge',
        60: 'Ledge',
        74: 'Redge',
        75: 'Ledge',
        89: 'Redge',
        90: 'Ledge',
        104: 'Redge',
        105: 'Ledge',
        119: 'Redge',
        120: 'Ledge',
        134: 'Redge',
        135: 'Ledge',
        149: 'Redge',
        150: 'Ledge',
        164: 'Redge',
        165: 'Ledge',
        179: 'Redge',
        180: 'Ledge',
        194: 'Redge',
        195: 'Ledge',
        209: 'Redge',
        210: 'Ledge',
        224: 'Redge'
      }
    });
    const boundaryArr = []; //保存边界key数组
    const latelyIndex = []; //保存棋盘上所有的下子的index
    onMounted(() => {
      initCoordinate(); //初始化坐标，边界下标
      totalCalculationTime(); //总计时
    });
    // 页面销毁清除定时器
    onBeforeUnmount(() => {
      clearInterval(data.totalTimes); //清除定时器
      clearInterval(data.bureauTimes); //清除定时器
    });
    // 游戏总计时
    const totalCalculationTime = () => {
      let time = 0;
      clearInterval(data.totalTimes); //清除定时器
      data.totalTimes = setInterval(() => {
        data.totalTimeNum = formatDuring(time); //获取天时分秒
        time += 1000;
      }, 1000);
    };
    // 本局游戏时间
    const bureauCalculationTime = () => {
      let time = 0;
      clearInterval(data.bureauTimes); //清除定时器
      data.bureauTimes = setInterval(() => {
        data.bureauTimeNum = formatDuring(time); //获取天时分秒
        time += 1000;
      }, 1000);
    };

    // 悔棋
    const fallbackChess = () => {
      if (!latelyIndex.length) return;
      data.coordinate[latelyIndex[latelyIndex.length - 1]].pieceType = 0; //取消最后一个index上的棋子
      data.currentPlayChess = data.currentPlayChess == 1 ? 2 : 1; //回退棋子类型
      data.currentPlayChess == 1
        ? data.blackLatelyNum++
        : data.whiteLatelyNum++;
      data.currentPlayChess == 1 ? data.blackChess++ : data.whiteChess++; //黑白子数量减少
      latelyIndex.pop(); //删除数组中最后一项
    };
    // 初始化坐标
    const initCoordinate = () => {
      bureauCalculationTime(); //本局计时
      data.victoryTips = ''; //胜利提示清除
      data.blackChess = 113; //黑棋数量
      data.whiteChess = 112; //白棋数量
      data.blackLatelyNum = 0; //黑色悔棋次数
      data.whiteLatelyNum = 0; //白色悔棋次数
      data.coordinate = []; //保存棋盘上所有点位对象信息
      data.currentPlayChess = 1;
      data.latelyIndex = [];
      // 遍历出边界key数组
      for (let key in data.boundary) {
        boundaryArr.push(parseInt(key));
      }
      // 循环创建xy轴位置
      let edgeRange; //范围表示
      let num = 0; //计数设置范围
      for (let x = 0; x < data.checkerboardNum + 1; x++) {
        for (let y = 0; y < data.checkerboardNum + 1; y++) {
          // 设置每行前8个和后7个范围
          num == 15 ? (num = 1) : num++; //15次重置一次
          edgeRange = num <= 8 ? 'Ledge' : 'Redge'; //每行前8Ledge 后7Redge
          // 创建点位对象
          data.coordinate.push({
            coor: [x * 50, y * 50], //点位置
            piece: false, //点是否有棋子
            pieceType: 0, //点上棋子类型
            edgeRange //点位所示左右范围
          });
        }
      }
    };
    // 下棋执行
    const playChess = (index, edgeRange) => {
      console.log('点击', index);
      //当前棋子类型 黑白
      const currentType = data.currentPlayChess;
      //  0取反   0代表位置空，没有棋子，可以下棋子
      if (!data.coordinate[index].pieceType) {
        data.coordinate[index].pieceType = currentType; //显示点击位置棋子类型
        currentType == 1 ? data.blackChess-- : data.whiteChess--; //黑白子数量减少
        data.currentPlayChess = currentType == 1 ? 2 : 1; //切换棋子类型
        latelyIndex.push(index); //保存当前index,悔棋使用
        fiveSubSuccess(index, currentType, edgeRange); //判断是否五子
      } else {
        alert('这里已经有棋子了，你看不见吗？'); //该位置存在棋子
      }
    };
    // 判断是否成5子
    const fiveSubSuccess = (index, type, edgeRange) => {
      let num = 0; //计算连续棋子个数
      // 1 →  15 ↓  16↘  14↗
      const directionConfig = [1, 15, 16, 14];
      directionConfig.forEach((dir) => {
        // 根据4个方向，查超出范围内容下标数组在判断五子
        getIndexList(index, dir, edgeRange).forEach((item) => {
          //计算相同颜色就++   遇到不同就归0
          if (data.coordinate[item].pieceType === type) {
            num++; //类型相等就++
            if (num === 5) fiveSubComplete(type);
            return;
          } else {
            //不等的就归零0
            num = 0;
          }
        });
      });
    };
    // 获取不同方向线的下标    当前位置  前后个4位下标 共9个下标
    const getIndexList = (index, num, edgeRange) => {
      let oneNum = index - num * 4; //计算出最小的下标，
      let list = []; //保存9位下标数组
      for (let i = 0; i < 9; i++) {
        const currentIndex = oneNum + num * i;
        if (currentIndex >= 0 && currentIndex <= data.pointNum - 1) {
          //1-225  0-224
          list.push(currentIndex);
        }
      }
      // 最后处理一下边界情况
      return boundaryHandle(list, index, num, edgeRange);
    };
    // 点击边界位置时处理
    // 判断数组中是否和边界数组中有交际
    const boundaryHandle = (list, index, num, edgeRange) => {
      // 先保存点击的下标（或范围数组中包含的边界下标）
      let myIndex = index;
      // 是否处理边界
      let result = false;
      // 点击的下标在不在边界上
      if (!data.boundary[myIndex]) {
        // 点击的不在边界上
        // 备份范围数组，方便后面取反，不改变原数组（取反是要解决左右范围处理边界错误）
        let mylist = JSON.parse(JSON.stringify(list));
        //  14↗方向并且左侧范围中，就反转数组
        if (num === 14 && edgeRange === 'Ledge') {
          mylist.reverse();
        }
        // 1 → 和16↘方向并且右侧范围中，就反转数组
        if ([1, 16].includes(num) && edgeRange === 'Redge') {
          mylist.reverse();
        }
        // 找出范围数组中元素是否有边界中的元素
        mylist.forEach((item) => {
          if (boundaryArr.includes(item)) {
            myIndex = item; //范围数组中包含的边界下标
            result = true; //进行边界处理
            return;
          }
        });
      } else {
        // 点击的在边界上  进行边界处理
        result = true;
      }
      // console.log('最终', myIndex);
      // console.log(list, boundaryArr);

      // 数组范围中包含边界下标，进行处理
      if (result) {
        console.log('边界处理');
        const listIndex = list.indexOf(myIndex); //获取当前点击下标在数组中的位置
        // 优化
        // 左侧边界处理
        if (data.boundary[myIndex] === 'Ledge') {
          //左边 → ↘方向  小于的不要
          if (num === 1 || num === 16) {
            list = list.slice(listIndex);
          }
          //左边 ↗方向   大于的不要
          if (num === 14) {
            list = list.slice(0, listIndex + 1);
          }
        }
        // 右侧边界处理
        if (data.boundary[myIndex] === 'Redge') {
          //右边 → ↘方向  大于的不要
          if (num === 1 || num === 16) {
            list = list.slice(0, listIndex + 1);
          }
          //右边 ↗方向   小于的不要
          if (num === 14) {
            list = list.slice(listIndex);
          }
        }
      }
      // console.log(list);
      return list;
    };

    // 五子完成提示
    const fiveSubComplete = (type) => {
      setTimeout(() => {
        data.victoryTips = type == 1 ? '黑方胜利' : '白方胜利'; //提示胜利
        type == 1 ? data.blackVictoryNum++ : data.whiteVictoryNum++;
      }, 100);
    };
    // 设置当前点击下棋黑白样式
    const judgeChessType = (type) => {
      return type == 1 ? 'black' : type == 2 ? 'white' : '';
    };
    return {
      initCoordinate,
      fallbackChess,
      judgeChessType,
      playChess,
      ...toRefs(data)
    };
  }
};
</script>
