      position: absolute;
      font-size: 150px;
      height: 750px;
      width: 750px;
      text-align: center;
      z-index: 888;
      font-weight: 700;
      line-height: 700px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      transition: 1s;
      &.black {
        color: #000;
