        margin: 3px;
        border-radius: 50%;
        background: rgba(0, 0, 0, 0);
        &.black {
          background-image: linear-gradient(to right bottom, #000, #000, #bbb);
          box-shadow: 1px 2px 4px 1px #000;
        }
        &.white {
          background-image: linear-gradient(to right bottom, #fff, #fff, #ccc);
          box-shadow: 1px 2px 4px 1px #000;
        }
      }
    }
  }
  .gobang-bottom {
    padding-bottom: 30px;
    .time-num {
      text-align: center;
      font-size: 16px;
      height: 20px;
      line-height: 20px;
      float: left;
