<template>
  <div class="Starsbox">
    <span
      v-for="number in 7"
      :key="number"
      :class="['iconfont Starsitem', number <= StarsNum ? 'active' : '']"
      :style="{
        fontSize: size + 'px',
        color: number <= StarsNum ? colorlist[StarsNum - 1] : color
      }"
      @click="clickStars(number)"
      @mouseover="mouseoverStars(number)"
      @mouseout="mouseoutStars"
    >
      &#xe662;
    </span>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, watch } from 'vue';
export default {
  name: 'Stars',
  props: {
    color: {
      type: String,
      default: 'rgb(197, 197, 197)'
    },
    num: {
      //初始选中几个星星
      type: Number,
      default: 0
    },
    size: {
      //星星大小
      type: Number,
      default: 16
    },
    isCLickActive: {
      //点击才选中星星
      type: Boolean,
      default: false
    },
    isMouseoverStyle: {
      //移入样式改变
      type: Boolean,
      default: true
    },
    colorArr: {
      //配置颜色
      type: Array,
      default: () => [
        '#006600',
        '#006666',
        '#003333',
        '#006699',
        '#000066',
        '#ff0066',
        '#ff0000'
      ]
    },
    readOnly: {
      //是否只读
      type: Boolean,
      default: false
    }
  },

  setup(props, ctx) {
    watch(
      // 监听数字变化更新组件星星数量
      () => props.num,
      (val) => {
        data.StarsNum = val;
      }
    );
    // watch(
    //   // 监听上下切换高亮
    //   () => props.colorArr,
    //   (newVal) => {},
    //   { immediate: true }
    // );
    const data = reactive({
      oldStarsNum: props.num,
      isClick: false, //是否点击
      StarsNum: props.num,
      colorlist: props.colorArr //颜色变化
    });

    onMounted(() => {});
    // 点击
    const clickStars = (num) => {
      if (!props.readOnly) {
        data.isClick = true;
        data.oldStarsNum = num;
        data.StarsNum = num;
        ctx.emit('getStarsnum', data.StarsNum);
      }
    };
    // 移入事件
    const mouseoverStars = (num) => {
      if (!props.readOnly) {
        if (props.isMouseoverStyle) {
          //取消移入样子
          data.isClick = false;
          data.StarsNum = num;
        }
      }
    };
    //移出事件，点击保留，不点击不选中
    const mouseoutStars = () => {
      if (!props.readOnly) {
        if (props.isCLickActive) {
          //点击才选中取消
          if (!data.isClick) {
            //点击了
            data.StarsNum = data.oldStarsNum; //移入的元素
          }
        } else {
          ctx.emit('getStarsnum', data.StarsNum);
        }
      }
    };
    return {
      mouseoutStars,
      mouseoverStars,
      clickStars,
      ...toRefs(data)
    };
  }
};
</script>
<style lang="less" scoped>
.Starsitem {
  margin: 0 1px;
  cursor: pointer;
  transition: 0.3s;
}
.Starsbox {
  overflow: hidden;
}
</style>
