<template>
  <div class="home">
    <!-- 全屏按键 -->
    <p
      @click="fullScreenSwitch"
      class="iconfont full-screen"
      :title="fullScreen ? '退出全屏' : '全屏'"
    >
      {{ fullScreen ? '\ue629' : '\ue62b' }}
    </p>
    <!-- 返回首页按键 -->
    <p
      title="返回首页"
      class="iconfont back-home icon-31fanhui1"
      v-if="isShowBackHomeBtn"
      @click="backHome"
    ></p>
    <!-- 页头部分 -->
    <div
      class="header"
      @mouseover="moveIn"
      @mouseleave="leave"
      :class="{ noheader: noheader }"
    >
      <div class="title">
        <!-- 网站logo -->
        <div class="logotext">
          <p class="name">SubTopH</p>
        </div>
        <!-- 大屏幕导航 -->
        <div class="nav">
          <div class="nav-item" v-for="item in navList" :key="item.path">
            <div
              class="nav-item-a"
              :title="item.name"
              @click="clickNavItem(item)"
              v-if="item.topNav"
            >
              <img :src="item.imgUrl" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 可移动元素 音乐播放器-->
    <h-portabilityBox
      :openPortability="openPortability2"
      :title="componentTitle2"
      :zIndex="22"
      :isZzc="true"
      :noPadding="true"
      :isBgColor="false"
      @closePortability="closePortability2"
    >
      <component
        :is="componentModul2"
        @frequencyArrChange="frequencyArrChange"
      ></component>
    </h-portabilityBox>
    <!-- 可移动元素 -->
    <h-portabilityBox
      :openPortability="openPortability"
      :title="componentTitle"
      :top="distanceTop"
      :zIndex="21"
      @closePortability="closePortability"
    >
      <component
        :is="componentModul"
        @addTaskList="addTaskList"
        :openList="openList"
      ></component>
    </h-portabilityBox>
    <div class="frequency-bottom" v-if="frequencyArr.lenght !== 0">
      <div
        v-for="(item, index) in frequencyArr"
        :key="index"
        :style="{
          height: (item - 120) * 2 + 'px',
          left: index * 38 + 20 + 'px'
        }"
        class="frequency-item"
      >
        <p class="frequency-z" v-for="itemz in 50" :key="itemz"></p>
      </div>
    </div>
    <!-- 底部内容部分，包括导航和内容 -->
    <div class="contentBox" :class="{ noheader: noheader }">
      <router-view @clickNavChange="clickNavChange" />
    </div>
  </div>
</template>

<script>
import {
  defineComponent,
  ref,
  reactive,
  onMounted,
  toRefs,
  watch,
  getCurrentInstance
} from 'vue';
import { navList } from './home.js';
import { useRouter, useRoute } from 'vue-router';
// 五子棋
import goBang from '@/pages/games/gobang.vue';
// 贪吃蛇
import gluttonous from '@/pages/games/gluttonous.vue';
// 激战
import warfarePk from '@/pages/games/warfarePk.vue';
// 激战
import musicPlayer from '@/pages/musicPlayer/musicPlayer.vue';
// 快速笔记
import note from '@/pages/note/note.vue';
// 快速笔记
import myTool from '@/pages/myTool/myTool.vue';
export default defineComponent({
  name: 'Home',
  components: { goBang, note, gluttonous, warfarePk, musicPlayer, myTool },
  setup() {
    const { proxy } = getCurrentInstance();
    const router = useRouter();
    const route = useRoute();
    const data = reactive({
      openList: false, //是否打开组件中列表
      isShowBackHomeBtn: false, //是否显示返回首页按键
      frequencyArr: [], //音频数组
      componentModul: '', //组件名称
      componentTitle: '', //组件模块title
      openPortability: false, //打开移动弹窗
      componentModul2: '', //组件名称
      componentTitle2: '', //组件模块title
      openPortability2: false, //打开移动弹窗
      noheader: true, //头部显示隐藏
      fullScreen: false, //是否全屏
      navList, //导航列表
      showTimes: null, //隐藏头部定时器
      hideTimes: null, //隐藏头部定时器
      distanceTop: '50px',
      distanceTop2: '50px'
    });
    watch(
      // 监听路由参数
      () => route.name,
      (val) => {
        data.isShowBackHomeBtn = val !== 'home';
      },
      { immediate: true } //立即执行一次
    );
    const showNavlist = ref(false); //移动端导航切换
    const screenWidth = ref(null); //页面宽度
    const actNavIndex = ref(null); //当前选中的导航
    onMounted(() => {
      // 监听f11全屏事件，禁止全屏，无法监听全屏下F11事件
      window.addEventListener('keydown', (e) => {
        e = e || window.event;
        if (e.key === 'F11') {
          e.preventDefault();
          fullScreen(); //进入全屏
        }
      });
      // 监听页面是否全屏,
      window.addEventListener('resize', isFullScreen);
      monitorWidth(); //监听页面宽度,响应式
      // 监听打开笔记面板,来自addTask组件
      proxy.$bus.on('openNote', openNote);
    });
    // 打开笔记
    const openNote = () => {
      //获取笔记模块对象
      const nav = navList.filter((item) => item.pathName == 'note')[0];
      // 赋值打开笔记模板
      data.openList = true; //打开笔记列表
      data.openPortability = true;
      data.componentModul = nav.pathName;
      data.componentTitle = nav.name;
      data.distanceTop = nav.top;
    };
    // 返回首页
    const backHome = () => {
      router.push({
        name: 'Home'
      });
    };
    // 音频变化
    const frequencyArrChange = (val) => {
      data.frequencyArr = val;
    };
    // 关闭弹窗
    const closePortability = () => {
      data.openPortability = false;
    };
    // 关闭2弹窗
    const closePortability2 = () => {
      data.openPortability2 = false;
      data.frequencyArr = [];
    };
    // 点击导航每一项事件
    const clickNavItem = (nav) => {
      // 导航类型是page就打开新页面
      if (nav.type === 'page') {
        router.push({ path: nav.path });
      } else {
        // 否则就以组件形式展示
        clickNavChange(nav);
      }
    };
    // 组件模块显示
    const clickNavChange = (nav) => {
      if (nav.zIndex === '2') {
        data.openPortability2 = true;
        data.componentModul2 = nav.pathName;
        data.componentTitle2 = nav.name;
        data.distanceTop2 = nav.top;
      } else {
        data.openPortability = true;
        data.componentModul = nav.pathName;
        data.componentTitle = nav.name;
        data.distanceTop = nav.top;
      }
    };
    // 全屏切换
    const fullScreenSwitch = () => {
      if (data.fullScreen) {
        signOutFullScreen(); //退出全屏
      } else {
        fullScreen(); //进入全屏
      }
    };
    // 判断是否是全屏
    const isFullScreen = () => {
      if (window.outerHeight === screen.availHeight) {
        if (window.outerWidth === screen.availWidth) {
          // 退出全屏后，js代码控制再次退出全屏，便于下次F11或点击进入全屏
          signOutFullScreen();
        } else {
          console.log('未使用');
        }
      } else {
        console.log('进入全屏');
      }
    };
    // 进入全屏
    const fullScreen = () => {
      data.fullScreen = true; //控制切换全屏
      let el = document.documentElement;
      let rfs =
        el.requestFullScreen ||
        el.webkitRequestFullScreen ||
        el.mozRequestFullScreen ||
        el.msRequestFullscreen;
      if (rfs) {
        // typeof rfs != "undefined" && rfs
        rfs.call(el);
      } else if (typeof window.ActiveXObject !== 'undefined') {
        // for IE，这里其实就是模拟了按下键盘的F11，使浏览器全屏
        let wscript = new ActiveXObject('WScript.Shell');
        if (wscript != null) {
          wscript.SendKeys('{F11}');
        }
      }
    };
    // 退出全屏
    const signOutFullScreen = () => {
      data.fullScreen = false; //控制切换全屏
      let el = document;
      let cfs =
        el.cancelFullScreen ||
        el.mozCancelFullScreen ||
        el.msExitFullscreen ||
        el.webkitExitFullscreen ||
        el.exitFullscreen;
      if (cfs) {
        // typeof cfs != "undefined" && cfs
        cfs.call(el);
      } else if (typeof window.ActiveXObject !== 'undefined') {
        // for IE，这里和fullScreen相同，模拟按下F11键退出全屏
        let wscript = new ActiveXObject('WScript.Shell');
        if (wscript != null) {
          wscript.SendKeys('{F11}');
        }
      }
    };
    // 移入显示头部
    const moveIn = () => {
      // 清除隐藏定时器
      clearTimeout(data.hideTimes);
      data.showTimes = setTimeout(() => {
        data.noheader = false;
      }, 100);
    };
    // 离开隐藏头部
    const leave = () => {
      clearTimeout(data.showTimes);
      data.hideTimes = setTimeout(() => {
        data.noheader = true;
      }, 1500);
    };
    // 点击移动端导航中导航时就关闭
    const clickNav = function () {
      showNavlist.value = false;
    };
    // 监听页面宽度
    const monitorWidth = function () {
      //初始化页面宽度
      screenWidth.value = document.body.clientWidth;
      window.addEventListener('resize', () => {
        //页面宽度变化继续监听
        screenWidth.value = document.body.clientWidth;
        // 如果窗口大于700就关闭移动端下拉导航
        if (screenWidth.value > 700) {
          showNavlist.value = false;
        }
      });
    };
    // 复制网站地址
    const copyUrL = function () {
      // 创建一个input元素
      const input = document.createElement('input');
      // 添加到body中
      document.body.appendChild(input);
      // 设置input的值
      input.setAttribute('value', 'https://subtoph.github.io');
      // 选择文本域内容
      input.select();
      // 执行浏览器复制命令
      // 复制命令会将当前选中的内容复制到剪切板中（这里就是创建的input标签）
      // Input要在正常的编辑状态下原生复制方法才会生效
      if (document.execCommand('copy')) {
        document.execCommand('copy');
      }
      alert('https://subtoph.github.io复制成功'); //提示
      // 清除元素
      document.body.removeChild(input);
    };
    // 将笔记添加至任务管理
    const addTaskList = (content) => {
      // 跳转任务管理页面，传递内容
      router.push({
        name: 'todoList',
        params: {
          content
        }
      });
    };

    return {
      openNote,
      backHome,
      addTaskList,
      frequencyArrChange,
      closePortability2,
      clickNavChange,
      clickNavItem,
      signOutFullScreen,
      fullScreen,
      fullScreenSwitch,
      leave,
      moveIn,
      closePortability,
      showNavlist,
      screenWidth,
      actNavIndex,
      copyUrL,
      clickNav,
      ...toRefs(data)
    };
  }
});
</script>

